.sidebar {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 5px;
  background: #e2f1ee;
}
.sidebar .item {
  text-align: center;
  margin: 10px;
}
.sidebar .item a {
  text-decoration: none;
}
.sidebar .link {
  color: black;
}
.sidebar .active {
  color: chocolate;
}

